.gpt3__cta {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 2rem;
	margin: 4rem;
	border-radius: 1rem;
	background: var(--gradient-bar);
}

.gpt3__cta-content p {
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 12px;
	line-height: 30px;
	color: #0e0e0e;
}

.gpt3__cta-content h3 {
	font-family: var(--font-family);
	font-weight: 800;
	font-size: 24px;
	line-height: 45px;
	color: #000000;
}

.gpt3__cta-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 2rem;
}

.gpt3__cta-btn button {
	background: #000000;
	color: white;
	width: 189.14px;
	height: 59.4px;
	border-radius: 40px;
	font-family: var(--font-family);
	font-weight: bold;
	font-size: 18px;
	line-height: 30px;
	outline: none;
	border: none;
	cursor: pointer;
}

@media screen and (max-width: 650px) {
	.gpt3__cta {
		flex-direction: column;
	}

	.gpt3__cta-content {
		text-align: center;
	}

	.gpt3__cta-btn {
		margin-top: 2rem;
	}
}

@media screen and (max-width: 550px) {
	.gpt3__cta {
		margin: 4rem 2rem;
	}

	.gpt3__cta-content h3 {
		font-size: 18px;
		line-height: 32px;
	}

	.gpt3__cta-content button {
		font-size: 14px;
		line-height: 28px;
	}
}
